
import React, { useEffect, useState } from 'react'
import { makeRequest } from '../../axios'
import "./follow.scss"
import User from './User';

function Followed({id}) {
  // console.log("f-id ", id)
  const [users, setUsers] = useState()
  async function fectchData (){
    let response = await makeRequest.get("/users/followed", {params : {id : id}});
    setUsers(response.data)
  }
    useEffect(()=>{
      fectchData()
    },[])
  
  return (
    <>
    <h3 style={{"textAlign" : "center"}}>Followers</h3>
      {!users ? "Not following Anyone" : users.map(u =>{
        return(
          <User id={u.id} profilePic={u.profilePic} name = {u.name}/>
        )
      })}
    </>
  )
}

export default Followed