import axios from 'axios';
import React, { useState } from 'react'
import { useNavigate } from 'react-router-dom';

function PasswordForm({email}) {
  console.log("p-email ", email)
    const [otpForm, setOtpForm] = useState({
        otpCode : "",
        password : "",
        cpassword : "",
    })
    const [err, setErr] = useState(null);
    const navigate = useNavigate()
    const handleChange = (e)=>{
        setOtpForm((prev) => ({ ...prev, [e.target.name]: e.target.value }))
    }

    const handlePassword = async (e)=>{
        e.preventDefault();
        try {
            let response = await axios.post("http://localhost:8800/api/users/change-password", {email : email, otp : otpForm.otpCode, password : otpForm.password, cpassword : otpForm.cpassword});

            setErr(response.data);
            if(response.data === "Password changed successfully"){
                setTimeout(() => {
                  navigate("/login");
                }, 2000);
                
            }
        } catch (error) {
            console.log(error)
        }
    }
  return (
    <>
        <h1>Reset Password</h1>
          <form>
            <input
              type="text"
              placeholder="OTP"
              name="otpCode"
              onChange={handleChange}
              maxLength = '4'
              value={otpForm.otpCode}
            />
            <input
              type="text"
              placeholder="Password"
              name="password"
              onChange={handleChange}
              value = {otpForm.password}
            />
            <input
              type="text"
              placeholder="Confirm Password"
              name="cpassword"
              onChange={handleChange}
              value = {otpForm.cpassword}
            />
            {err && err}
            <button onClick={handlePassword}>Reset Password</button>
          </form>
    </>
  )
}

export default PasswordForm