import {useState } from "react";
import { Link } from "react-router-dom";
import "../login/login.scss";
import axios from "axios";
import PasswordForm from "../PasswordForm";

const Forgot = () => {
  const [email, setEmail] = useState("");
  const [err, setErr] = useState(null);
  const [otpForm, showForm] = useState(false);




  const handleOtp = async (e) => {
    e.preventDefault();
    try {
      let response = await axios.post("http://localhost:8800/api/users/email-send", {email});
      console.log("response >> ", response);
      if(response.data !== "Please Check your Email"){
        setErr(response.data);
      }
      else{
        setErr(response.data);
        setTimeout(() => {
          showForm(true);
        }, 2000);
      }
      
      
    } catch (err) {
      setErr(err.response.data);
    }
  };

  return (
    <div className="login">
      <div className="card">
        <div className="left">
          <h1>Hello World.</h1>
          <p>
            Lorem ipsum dolor sit amet consectetur adipisicing elit. Libero cum,
            alias totam numquam ipsa exercitationem dignissimos, error nam,
            consequatur.
          </p>
          <span>Don't you have an account?</span>
          <Link to="/register">
            <button>Register</button>
          </Link>
        </div>
        <div className="right">
          {!otpForm ? <><h1>Forgot Password</h1>
          <form>
            <input
              type="text"
              placeholder="Email"
              name="email"
              onChange={(e)=> setEmail(e.target.value)}
              value={email}
            />
            {err && err}
            <button onClick={handleOtp}>Send OTP</button>
          </form></> : <PasswordForm email={email}/>}
        </div>
      </div>
    </div>
  );
};

export default Forgot;
