import React from 'react'
import { Link } from 'react-router-dom'

function User({id, profilePic, name}) {
  return (
    <Link to={"/profile/" + id} style={{ textDecoration: "none", color: "inherit" }}>
        <div className="row" key={id}>
            <img src={"/upload/" + profilePic}alt="" />
            <h4>{name}</h4>
        </div>
    </Link>
  )
}

export default User